@import '../../theme.scss';
.backdrop {
	z-index: -1;
	position: fixed;
	right: 0;
	bottom: 0;
	top: 0;
	left: 0;
	background-color: rgba(0, 0, 0, 0.5);
	// -webkit-tap-highlight-color: transparent;
	opacity: 1;
}
.createQuizBtn {
	background-color: #fcc822;
	color: white;
	font-family: 'Poppins';
	font-size: 1.5rem;
	width: fit-content;
	border: none;
	padding: 0.8rem 2rem;
	font-weight: 600;
	box-shadow: 0 10px 10px -10px rgba(0, 0, 0, 0.5);
	&:hover {
		-webkit-transform: translateY(-5px);
		transform: translateY(-5px);
	}
}
.closeIcon {
	color: #d1d1d1;
}
.createQuizNameTypePage {
	background-color: white;
	//keep modal in center
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);
	width: 85%;
	//spacing
	padding: 1rem 1rem;
	text-align: right;
}

@include media($phone) {
	.createQuizNameTypePage {
		padding: 0.5rem 0;
	}
	.createQuizBtn {
		font-size: 1rem;
		padding: 0.8rem 2rem;
	}
}
