@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

// responsive styling tool
$wide: '(min-width: 1800px)' !default;
$desktop: '(min-width: 1200px)' !default;
$tablet-landscape: '(min-width: 900px) and (orientation: landscape)' !default;
$tablet-protrait: '(min-width: 600px) and (orientation: portrait)' !default;
$phone: '(max-width: 600px)' !default;

@mixin media($query: $phone) {
	@media #{$query} {
		@content;
	}
}

// theme colors
$theme_color: #fcc822;
$text_color: #383838;

a {
	text-decoration: none;
}
