@import '../../theme.scss';

@mixin buttonStyle {
	width: 150px;
	height: 40px;
	font-weight: bold;
	border: 1px solid $theme_color;
	background-color: white;
	color: $theme_color;
	cursor: pointer;
}

@mixin inputStyle {
	width: 100%;
	border: none;
	height: 30px;
	background-color: transparent;
	box-sizing: border-box;
	transition: 400ms all ease-in-out;
	outline: none;
}

@mixin labelStyle {
	width: 100%;
	text-align: left;
	color: $text_colot;
}

.signup__container {
	width: 100vw;
	height: 100vh;
	display: flex;
	.signup__back {
		color: rgb(104, 104, 104);
		cursor: pointer;
		font-size: 20px;
		display: flex;
		align-items: center;
		justify-content: center;
		position: absolute;
		top: 20px;
		left: 20px;
		p {
			font-size: 15px;
			margin-left: 10px;
		}
	}
	.signup__form {
		width: 50%;
		height: 100vh;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		.signup__box {
			width: 50%;
			margin: 40px 0 20px 0;
		}
		section {
			margin-top: 30px;
			p {
				margin: 15px 0;
				color: $text_color;
				text-align: center;
			}
		}

		img {
			width: 300px;
		}
		.signup__btnWrapper {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			button {
				@include buttonStyle();
				margin: 10px;
				width: 50%;
				background-color: $theme_color;
				color: white;
			}
		}
		.signup__redirect {
			margin-top: 20px;
			color: $text_color;
			span {
				color: $theme_color;
				cursor: pointer;
			}
		}
	}
	aside {
		width: 50%;
		height: 100vh;
		background-image: url('../../assets/Signup-hero.png');
		background-size: cover;
		background-position: center center;
		position: relative;

		.signup__overlay {
			position: absolute;
			top: 0;
			left: 0;
			width: 50vw;
			height: 100vh;
			background-color: #4949492c;
			backdrop-filter: blur(40px);
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			.signup__textContainer {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;
				margin: 0 20px;
				width: 80%;

				.signup__hero {
					width: 100%;
				}
			}
		}
	}
}

@include media($phone) {
	.signup__container {
		.signup__form {
			width: 100vw;
			.signup__box {
				width: 80%;
			}
		}
		aside {
			display: none;
		}
	}
}

@include media($tablet-protrait) {
	.signup__container {
		.signup__form {
			width: 100vw;
			.signup__box {
				width: 80%;
			}
		}
		aside {
			display: none;
		}
	}
}
