@import '../../theme.scss';

.MultipleChoiceContainer {
	background-color: $theme_color;
	width: 52.5%;
	max-width: 600px;
	min-height: 210px;
	border-radius: 9px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	box-shadow: 0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%);
	.typeQuestion {
		width: 50%;
		margin-left: auto;
		margin-right: auto;
		background-color: $theme_color;
	}
	input {
		color: white;
		text-align: center;
		color: white;
		font-size: 15px;
	}
	.answerCard {
		position: relative;
		ul {
			display: flex;
			flex-wrap: wrap;
			list-style-type: none;
			display: flex;
			justify-content: space-evenly;
			width: 100%;
			padding-left: 0;
			li {
				flex-basis: calc(25% - 10px);
				height: 135px;
				border-radius: 9px;
				margin-bottom: 20px;
				justify-content: space-evenly;
				display: flex;
				flex-direction: column;
				box-shadow: 0px 3px 1px -2px rgb(0 0 0 / 20%), 0px 2px 2px 0px rgb(0 0 0 / 14%),
					0px 1px 5px 0px rgb(0 0 0 / 12%);
				label {
					margin: 0;
					margin-top: -15px;
					padding: 0;
					color: white;
					display: flex;
					justify-content: space-around;
					background: rgba(255, 255, 255, 0.42);
					border-radius: 4px;
					height: 19px;
					margin-left: 5px;
					margin-right: 5px;
					span {
						margin: 0;
						padding: 0;
						font-size: 7px;
						svg {
							font-size: 1rem;
						}
					}
				}
				div {
					max-width: 69px;
					margin-left: auto;
					margin-right: auto;
					max-height: 42px;
					top: -6px;
					input {
						position: relative;
						font-size: 10px;
						padding: 0;
						margin: 0;
						width: 100%;
						height: 42px;
						&::after {
							border-bottom: 2px solid white;
						}
					}
				}
				&:nth-child(1) {
					background-color: #c2aeed;
					input {
						background-color: #c2aeed;
					}
				}
				&:nth-child(2) {
					background-color: #f7a588;
					input {
						background-color: #f7a588;
					}
				}
				&:last-child {
					background-color: #fc7e22;
					color: white;
					font-size: 10px;
					align-items: center;
					span {
						width: 69px;
						height: 42px;
						text-align: center;
						cursor: pointer;
					}
					button {
						font-size: 10px;
						width: 69px;
						height: 42px;
						cursor: pointer;
						background-color: transparent;
						border: transparent;
						color: white;
					}
				}
			}
			.close {
				display: none;
			}
		}
	}
	.btnFooter {
		margin-bottom: 20px;
		margin-right: 30px;
		display: flex;
		align-items: center;
		justify-content: flex-end;
		gap: 20px;
		.btnSave {
			color: white;
			background-color: #fddf7f;
			text-transform: capitalize;
			height: 20px;
			width: 36px;
			span {
				font-size: 9px;
			}
		}
		.btnCancel {
			color: white;
			background-color: #fddf7f;
			text-transform: capitalize;
			height: 20px;
			width: 44px;
			span {
				font-size: 9px;
			}
		}
	}
}

@include media($tablet-protrait) {
	.MultipleChoiceContainer {
		width: 50%;
	}
}

@include media($phone) {
	.MultipleChoiceContainer {
		width: 100%;
		height: 100%;
		.typeQuestion {
			top: 0;
			margin-top: 10px;
		}
		.answerCard {
			bottom: 0;
			ul {
				flex-direction: column;
				width: 100%;
				align-items: center;
				li {
					flex-basis: auto;
					margin-bottom: 20px;
					width: 70%;
				}
			}
		}
		.btnFooter {
			justify-content: center;
			margin-bottom: 15px;
			margin-top: 10px;
		}
	}
}
