@import '../../theme.scss';

@mixin buttonStyle {
	width: 150px;
	height: 40px;
	font-weight: bold;
	border: 1px solid $theme_color;
	background-color: white;
	color: $theme_color;
	cursor: pointer;
}

@mixin inputStyle {
	width: 100%;
	border: none;
	height: 30px;
	background-color: transparent;
	box-sizing: border-box;
	transition: 400ms all ease-in-out;
	outline: none;
}

@mixin labelStyle {
	width: 100%;
	text-align: left;
	color: $text_colot;
}

.login__container {
	width: 100vw;
	height: 100vh;
	display: flex;
	.login__back {
		color: rgb(104, 104, 104);
		cursor: pointer;
		font-size: 20px;
		display: flex;
		align-items: center;
		justify-content: center;
		position: absolute;
		top: 20px;
		left: 20px;
		p {
			font-size: 15px;
			margin-left: 10px;
		}
	}
	.login__form {
		width: 50%;
		height: 100vh;
		display: flex;
		align-items: center;
		justify-content: center;
		flex-direction: column;
		.login__box {
			width: 50%;
			margin: 40px 0;
		}
		section {
			margin-top: 30px;
			p {
				margin: 15px 0;
				color: $text_color;
				text-align: center;
			}
		}

		img {
			width: 300px;
		}
		.login__btnWrapper {
			width: 100%;
			display: flex;
			align-items: center;
			justify-content: center;
			button {
				@include buttonStyle();
				margin: 10px;
				&:first-child {
					background-color: $theme_color;
					color: white;
				}
			}
		}
	}
	aside {
		width: 50%;
		height: 100vh;
		background-image: url('../../assets/Login-banner.png');
		background-size: cover;
		background-position: center center;
		position: relative;

		.login__overlay {
			position: absolute;
			top: 0;
			left: 0;
			width: 50vw;
			height: 100vh;
			background-color: #1f1f1f69;
			backdrop-filter: blur(15px);
			display: flex;
			align-items: center;
			justify-content: center;
			flex-direction: column;
			.login__textContainer {
				position: relative;
				display: flex;
				align-items: center;
				justify-content: center;
				flex-direction: column;
				margin: 0 20px;
				width: 80%;
				.login__quotMark {
					position: absolute;
					top: 0;
					left: 0;
				}
				.login__rightAngleMark {
					position: absolute;
					bottom: 0;
					right: 0;
				}
				p {
					width: 60%;
					line-height: 40px;
					font-size: 25px;
					color: $theme_color;
					text-align: center;
				}
				.login__author {
					font-size: 25px;
					color: white;
					width: 60%;
					line-height: 40px;
					text-align: center;
				}
			}
		}
	}
}

@include media($phone) {
	.login__container {
		.login__form {
			width: 100vw;
			.login__box {
				width: 80%;
			}
		}
		aside {
			display: none;
		}
	}
}

@include media($tablet-protrait) {
	.login__container {
		.login__form {
			width: 100vw;
			.login__box {
				width: 80%;
			}
		}
		aside {
			display: none;
		}
	}
}
