@import '../../theme.scss';

.questions__container {
	> span {
		display: block;
		text-align: center;
		font-weight: 500;
	}
	@include media($phone) {
		padding: 0 50px;
	}
}
.questions__content {
	margin: auto;
	width: 365px;
	display: flex;
	justify-content: space-evenly;
	border-radius: 9px;
	background-color: $theme_color;
	box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
	button {
		margin: 20px;
		&:hover {
			color: #ffff;
		}
	}
	@include media($phone) {
		width: 80%;
		flex-flow: column;
		align-items: center;
	}
}
