@import '../../theme.scss';

* {
	margin: 0px;
	padding: 0px;
	// box-sizing: border-box;
}
.landing-subtitle-des::after {
	content: '';
	width: 2px;
	height: 34px;
	position: absolute;
	top: -8px;
	left: 0;
	background: black;
}
.landing-title {
	width: 310px;
	height: 130.63px;
	font-family: 'Poppins';
	font-style: normal;
	font-weight: 500;
	font-size: 34.1564px;
	line-height: 44px;
	color: #333333;
	padding-left: 15px;
	margin: 18px auto;
}

// .landing-subtitle-line {
// 	height: 55px;
// 	width: 31px;
// }

.landing-subtitle {
	display: flex;
	align-items: center;
	justify-content: center;
	margin: 15px 0;
}

.landing-subtitle-des {
	font-size: 13.9337px;
	line-height: 16px;
	color: #828282;
	position: relative;
	padding-left: 8px;
	margin: 10px;
}

.landing-button {
	display: flex;
	justify-content: center;
	margin: 24px 100px 0 42px;
	align-items: center;
}

.landing-button-left button {
	margin: 10px;
}

.landing-button-right {
	color: #fcc822;
	height: 16px;
	padding-bottom: 8px;
	padding-left: 8px;
	overflow: hidden;
}
.svg-inline--fa {
	display: inline-block;
	font-size: inherit;
	height: 1em;
	overflow: visible;
	vertical-align: 0em;
}

.landing-img-left img {
	height: 250px;
	width: 300px;
}

.landing-img {
	position: absolute;
	bottom: 0;
	left: 0;
}

.landing-content {
	display: flex;
	justify-content: center;
	align-items: center;
	position: relative;
	/* min-width: 1000px; */
}

.right {
	flex: 1;
}

.right img {
	width: 80%;
	height: 80%;
}

.left {
	position: relative;
	flex: 1;
}

@media screen and (max-device-width: 425px) {
	.landing-content {
		/* display: none; */
		display: flex;
		flex: 1;
		flex-flow: column;
		justify-content: stretch;
		align-content: center;
	}
	.landing-title {
		width: 310px;
		height: 130.63px;
		font-family: Roboto;
		font-style: normal;
		font-weight: 500;
		font-size: 37.1564px;
		line-height: 44px;
		color: #333333;
		padding-left: 15px;
		margin: 10 auto;
	}
	.right {
		width: 400px;
		display: flex;
		justify-content: center;
	}
	.landing-img-left img {
		height: 0;
		width: 0;
	}
	.landing-button {
		display: flex;
		justify-content: center;
		/* margin: 24px 100px 0 42px; */
		width: 180px;
		align-items: center;
		overflow: hidden;
	}
}
