@import '../../theme.scss';

header {
	background-color: rgba($color: #ffffff, $alpha: 1);
	display: flex;
	padding: 5px 15px;
	width: 240vw;
	height: 78.96px;
	top: 21.8px;
	border-bottom: 0.580569px solid #ecece8;
	p {
		display: flex;
		word-wrap: break-word;
		width: 80px;
		height: 45px;
		margin-top: 20.84px;
		margin-left: 16.75px;
		line-height: 25px;
		color: $theme_color;
		font-family: 'Shanti', sans-serif;
		font-size: 20px;
	}
	.logo-section {
		display: flex;
		flex-direction: row;
		img {
			margin-left: 51.8px;
			margin-top: 21.8px;
			margin-bottom: 16.96px;
		}
	}
	.login-area {
		position: absolute;
		right: -23px;
		padding: 27px;
		padding-right: 55px;
		color: #e0e0e0;
		span {
			font-family: 'Poppins', sans-serif;
			font-size: 13.9337px;
			line-height: 36.58px;
			margin-inline-end: 23.22px;
			white-space: nowrap;
		}
		Button {
			font-family: 'Poppins', sans-serif;
			padding: 0.1px 0.1px;
			display: inline-block;
			font-size: 13.93px;
			color: $theme_color;
			background-color: #ffffff;
			height: 32.51px;
			width: 75px;
			border-radius: 1px;
			border-color: $theme_color;
			text-transform: lowercase;
			text-transform: capitalize;
		}
	}
}
