@import '../../theme.scss';
li {
	list-style-type: none;
}
.inputName_Tag {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
}
.quizName {
	margin-top: 3rem;
	width: 40%;
	height: 4rem;
	outline: none;
	border: 0.1rem solid $theme_color;
	box-sizing: border-box;
	font-size: 1.5rem;
}
.notice {
	font-size: 1.5rem;
	font-weight: 600;
}

.quizTag {
	background-color: #d1d1d1;
	border: none;
	width: fit-content;
	font-family: 'Poppins';
	font-size: 1.2rem;
	font-weight: 650;
	padding: 0 0.5rem;
	&:hover {
		background-color: $theme_color;
	}
}
.selectedQuizTag {
	background-color: $theme_color;
}
.CancelSelectedQuizTag {
	color: white;
	background-color: black;
	font-weight: 500;
	padding: 0 0.5rem;
}
.quizTagSelectSection {
	margin: 2% 15%;
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: center;
}
.selctType {
	padding: 0rem 0.8rem;
	margin: 0.5rem 0;
}
.addTagBtn {
	color: white;
	width: 60px;
	padding: 0 2rem;
	margin: 0.8rem 0;
	background-color: $theme_color;
	&:hover {
		font-weight: 800;
	}
	&::placeholder {
		color: white;
		text-align: center;
	}
}
.startQuizBtn {
	background-color: $theme_color;
	color: white;
	font-family: 'Poppins';
	font-size: 1.5rem;
	width: fit-content;
	border: none;
	box-shadow: 0 10px 10px -10px rgba(0, 0, 0, 0.5);
	&:hover {
		-webkit-transform: translateY(-5px);
		transform: translateY(-5px);
	}
}

@include media($desktop) {
	.quizName {
		margin-top: 3rem;
	}
	.quizTag {
		font-size: 1.5rem;
		font-weight: 650;
		padding: 0 0.5rem;
	}
	.selctType {
		padding: 0rem 0.8rem;
		margin: 1rem 0;
	}
	.startQuizBtn {
		font-size: 1.5rem;
		padding: 0.8rem 2rem;
		position: relative;
		left: 40%;
		margin: auto;
	}
}
@include media($phone) {
	.quizName {
		margin-top: 1rem;
		height: 2rem;
		font-size: 1rem;
	}
	.notice {
		font-size: 1rem;
	}
	.quizTag {
		font-size: 0.9rem;
		font-weight: 650;
		padding: 0 0.5rem;
	}
	.startQuizBtn {
		font-size: 1rem;
		padding: 0.8rem 2rem;
	}
	.quizTagSelectSection {
		margin: 2% 5%;
	}
}
